import Vue from 'vue'
import Router from 'vue-router'
// import Recommend from 'components/recommend/recommend'
// import Singer from 'components/singer/singer'
// import Rank from 'components/rank/rank'
// import Search from 'components/search/search'
// import SingerDetail from "components/singer-detail/singer-detail"
// import Disc from 'components/disc/disc'
// import TopList from 'components/top-list/top-list'

const Recommend = (resolve) => {
    import ('components/recommend/recommend').then((recommend) => {
        resolve(recommend)
    })
}

const Singer = (resolve) => {
    import ('components/singer/singer').then((moudle) => {
        resolve(moudle)
    })
}

const Rank = (resolve) => {
    import ('components/rank/rank').then((moudle) => {
        resolve(moudle)
    })
}

const Search = (resolve) => {
    import ('components/search/search').then((moudle) => {
        resolve(moudle)
    })
}

const SingerDetail = (resolve) => {
    import ('components/singer-detail/singer-detail').then((moudle) => {
        resolve(moudle)
    })
}

const Disc = (resolve) => {
    import ('components/disc/disc').then((moudle) => {
        resolve(moudle)
    })
}

const TopList = (resolve) => {
    import ('components/top-list/top-list').then((moudle) => {
        resolve(moudle)
    })
}

Vue.use(Router)

export default new Router({
    routes: [{
            path: '/',
            redirect: '/recommend'
        },
        {
            path: '/recommend',
            component: Recommend,
            children: [{
                path: ':id',
                component: Disc
            }]
        },
        {
            path: '/singer',
            component: Singer,
            children: [{
                path: ':id',
                component: SingerDetail

            }]
        },
        {
            path: '/rank',
            component: Rank,
            children: [{
                path: ':id',
                component: TopList
            }]
        },
        {
            path: '/search',
            component: Search,
            children: [{
                path: ':id',
                component: SingerDetail
            }]
        }
    ]
})


